/* Styling for the button and icon */
.btn-link {
position: relative;
padding-right: 30px; /* Adjust space for the icon */
}

.icon-toggle {
margin-left: auto; /* Pushes the icon to the far right */

position: absolute;
right: 0; /* Adjust positioning */
top: 50%;
transform: translateY(-50%);
transition: transform 0.3s ease;
}

/* Rotate the icon when the accordion is open */
.collapse.show ~ .btn-link .icon-toggle {
transform: translateY(-50%) rotate(90deg); /* Rotate the icon to point down */
}
.forgot-user-id-card {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  max-width: 400px;
  padding: 20px;
  background-color: white;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  z-index: 1000;
  text-align: left;
  font-family: Arial, sans-serif;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 18px;
  cursor: pointer;
}

.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
}

.mitid-link__label {
  cursor: pointer;
  color: #0060e6;
}

