  font-weight: bold;
}


.loader-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: white;
  z-index: 1000;
}
.loader-icon {
display: flex;
justify-content: center; /* Center horizontally */
align-items: center; /* Center vertically */
margin-bottom: 50px;
width: 60px; /* Slightly smaller width */
height: 60px; /* Slightly smaller height */
fill: #0060e6; /* Fill color for SVG */
}

.loader-icon svg {
width: 100%;
height: 100%;
}

.loader-shield {
display: flex;
justify-content: center; /* Center horizontally */
align-items: center; /* Center vertically */
width: 120px; /* Size of the shield */
height: 120px; /* Size of the shield */
position: relative; /* Allow positioning adjustments */
left: 10px; /* Move shield slightly to the right */
}

.loader-shield svg {
width: 100%;
height: 100%;
fill: #0060e6; /* Fill color */
}

.rtr {
position: absolute;
border: 4px solid rgba(255, 255, 255, 0.3); /* Light border */
border-top: 4px solid #ffffff; /* White border */
border-radius: 50%;
width: 50px; /* Slightly increased size */
height: 50px; /* Slightly increased size */
animation: spin 1s linear infinite;
top: 30%; /* Positioning from the top */
left: 20%; /* Slightly moved to the left */
transform: translate(-50%, -50%); /* Center by translating */
}

/* Keyframes for spinner animation */
@keyframes spin {
0% { transform: rotate(0deg); }
100% { transform: rotate(360deg); }
}




